.wavy-box {
    --s: 6px;
    --w: 420px;
    width: round(var(--w), 4*var(--s));
    aspect-ratio: unset;
    min-height: 285px;
    border: calc(var(--s) * 2.2) solid #0000;
    box-sizing: border-box;
    background: #1E1E1E;
    border-radius: 16px;
    -webkit-mask:
        radial-gradient(calc(sqrt(1.5)*var(--s)), #000 calc(100% - 0.7px), #0000),
        conic-gradient(#000 0 0) content-box,
        radial-gradient(calc(sqrt(1.5)*var(--s)), #0000 100%, #000 calc(100% + 0.7px)) var(--s) var(--s) padding-box;
            mask:
        radial-gradient(calc(sqrt(1.5)*var(--s)), #000 calc(100% - 0.7px), #0000),
        conic-gradient(#000 0 0) content-box,
        radial-gradient(calc(sqrt(1.5)*var(--s)), #0000 100%, #000 calc(100% + 0.7px)) var(--s) var(--s) padding-box;
    -webkit-mask-size: calc(var(--s)*4) calc(var(--s)*4);
            mask-size: calc(var(--s)*4) calc(var(--s)*4);
}

